.element {
    margin: 3vw auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.element-section {
    width: 200px;
    margin: 1vw 2px;
    height: 350px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.4s;
}

.element-title {
    width: 100%;
    min-height: 70px;
    bottom: 0;
    position: absolute;
    background-color: black;
    color: white;
    text-align: center;
}

.element-section img {
    max-width: 100%; 
}

.element-section h1 {
    padding: 0.4vw;
    margin-top: 0.5vw;
    font-size: 1vw;
}

.element-section:hover {
    box-shadow: 0 4px 15px black;
}

@media screen and (max-width: 800px) {
    .element-section h1 {
        font-size: 2vw;
    }
}

@media screen and (max-width: 500px) {
    .element {
        flex-direction: column;
        align-items: center;
        justify-content: none;
    }

    .element-section {
        width: 65vw;
        height: 100vw;
    }

    .element-title {
        justify-content: center;
        display: flex;
        align-items: center;
    }

    .element-section h1 {
        font-size: 4vw;
    }
}
