.search {
    background-color: lightgrey;
    text-align: center;
    padding: 5vw;
}

.search > * {
    padding: 1.6vw 0.6vw;
    font-size: 1.5vw;
    border: none;

}

.search input {
    width: 50vw;
}

.search button {
    width: 10vw;
    background-color: black;
    color: white;
    cursor: pointer;
    transition: all 0.4s;
    box-shadow: none;
}

.search button:hover {
    box-shadow: 0 1px 5px black;
}

.search > *:focus {
    outline: none;
    border: none;
}


@media screen and (max-width: 500px) {
    .search > * {
        width: 100%;
        padding: 3vw 1.5vw;
        font-size: 4vw;
        border: none;
    
    }
    
    .search input {
        width: 50vw;
    }
    
    .search button {
        width: 17vw;
    }
    
    .search button:hover {
        box-shadow: none;
    }

}