.bigElement {
    width: 60%;
    background-color: black;
    color: white;
    display: flex;
    padding: 2vw;
    position: absolute;
    top: 15vh;
    right: 50%;
    transform: translateX(50%) scale(0);
    transition: all 0.2s;
    z-index: 123;
}

.showElement {
    transform: translateX(50%) scale(1);
}

.bigImage {
    margin: 0 1vw;
}

.bigImage img {
    max-width: 30vw;
}


.bigAbout h1{
    font-size: 2vw;
    margin-bottom: 1vw;
    text-align: center;
    border-bottom: 2px solid;
}

.bigAbout h2 {
    font-size: 1.8vw;
}

.bigAbout span {
    font-size: 1.3vw;
}

.dark {
    left: 0;
    position: absolute;
    width: 100%;
    height: 200%;
    background-color: black;
    opacity: 0.7;
    top: 0;
    display: none;
    cursor: pointer;
}

.showDark {
    display: block;
}

@media screen and (max-width: 800px) {
    .bigElement {
        width: 75%;
    }

    .bigImage img {
        max-width: 35vw;
    }

    .bigAbout h2 {
        font-size: 2.2vw;
    }

    .bigAbout h1 {
        font-size: 2.4vw;
    }

    .bigAbout span {
        font-size: 1.6vw;
    }

}

@media screen and (max-width: 500px) {
    .bigElement {
        width: 75%;
        flex-direction: column;
    }

    .bigImage {
        text-align: center;
    }

    .bigImage img {
        max-width: 45vw;
    }

    .bigAbout h2 {
        font-size: 3vw;
    }

    .bigAbout h1 {
        font-size: 3.3vw;
    }

    .bigAbout span {
        line-height: 0.2vw;
        font-size: 2.4vw;
    }


}